"use client";

import { useState, useEffect } from 'react';
import { Save } from 'lucide-react';

export default function SettingsPage() {
    const [loading, setLoading] = useState(false);
    const [fetching, setFetching] = useState(true);
    const [formData, setFormData] = useState({
        hero: { title: '', subtitle: '', ctaText: '' },
        about: { title: '', description: '', skills: '' },
        contact: { email: '', phone: '', linkedin: '', github: '', twitter: '', resume: '' }
    });

    useEffect(() => {
        fetch('/api/settings')
            .then(res => res.json())
            .then(data => {
                if (data) {
                    setFormData({
                        hero: data.hero || { title: '', subtitle: '', ctaText: '' },
                        about: {
                            title: data.about?.title || '',
                            description: data.about?.description || '',
                            skills: data.about?.skills?.join(', ') || ''
                        },
                        contact: {
                            email: data.contact?.email || '',
                            phone: data.contact?.phone || '',
                            linkedin: data.contact?.linkedin || '',
                            github: data.contact?.github || '',
                            twitter: data.contact?.twitter || '',
                            resume: data.contact?.resume || ''
                        }
                    });
                }
                setFetching(false);
            })
            .catch(err => {
                console.error(err);
                setFetching(false);
            });
    }, []);

    const handleChange = (section: string, field: string, value: string) => {
        setFormData(prev => ({
            ...prev,
            [section]: {
                ...prev[section as keyof typeof prev],
                [field]: value
            }
        }));
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setLoading(true);

        const payload = {
            ...formData,
            about: {
                ...formData.about,
                skills: formData.about.skills.split(',').map(s => s.trim()).filter(s => s !== '')
            }
        };

        try {
            const res = await fetch('/api/settings', {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(payload)
            });
            if (res.ok) {
                alert('Settings saved!');
            } else {
                alert('Failed to save settings.');
            }
        } catch (error) {
            console.error(error);
            alert('Error saving settings.');
        } finally {
            setLoading(false);
        }
    };

    if (fetching) return <div className="p-8 text-secondary">Loading settings...</div>;

    return (
        <div>
            <h1 className="text-3xl font-display text-primary mb-8">Site Settings</h1>
            <form onSubmit={handleSubmit} className="space-y-12 max-w-4xl">

                {/* Hero Section */}
                <section className="space-y-6">
                    <h2 className="text-xl font-bold text-white border-b border-border pb-2">Hero Section</h2>
                    <div className="grid grid-cols-1 gap-6">
                        <div>
                            <label className="block text-sm font-medium text-secondary mb-1">Main Heading</label>
                            <input
                                value={formData.hero.title}
                                onChange={(e) => handleChange('hero', 'title', e.target.value)}
                                className="w-full bg-surfaceHighlight border border-border rounded p-2 text-primary focus:outline-none focus:border-accent"
                            />
                        </div>
                        <div>
                            <label className="block text-sm font-medium text-secondary mb-1">Subtitle</label>
                            <textarea
                                value={formData.hero.subtitle}
                                onChange={(e) => handleChange('hero', 'subtitle', e.target.value)}
                                className="w-full bg-surfaceHighlight border border-border rounded p-2 text-primary focus:outline-none focus:border-accent"
                                rows={2}
                            />
                        </div>
                    </div>
                </section>

                {/* About Section */}
                <section className="space-y-6">
                    <h2 className="text-xl font-bold text-white border-b border-border pb-2">About Section</h2>
                    <div className="grid grid-cols-1 gap-6">
                        <div>
                            <label className="block text-sm font-medium text-secondary mb-1">About Heading</label>
                            <input
                                value={formData.about.title}
                                onChange={(e) => handleChange('about', 'title', e.target.value)}
                                className="w-full bg-surfaceHighlight border border-border rounded p-2 text-primary focus:outline-none focus:border-accent"
                            />
                        </div>
                        <div>
                            <label className="block text-sm font-medium text-secondary mb-1">Bio Description</label>
                            <textarea
                                value={formData.about.description}
                                onChange={(e) => handleChange('about', 'description', e.target.value)}
                                className="w-full bg-surfaceHighlight border border-border rounded p-2 text-primary focus:outline-none focus:border-accent"
                                rows={4}
                            />
                        </div>
                        <div>
                            <label className="block text-sm font-medium text-secondary mb-1">Skills (Comma separated)</label>
                            <input
                                value={formData.about.skills}
                                onChange={(e) => handleChange('about', 'skills', e.target.value)}
                                className="w-full bg-surfaceHighlight border border-border rounded p-2 text-primary focus:outline-none focus:border-accent"
                                placeholder="React, Node.js, Design"
                            />
                        </div>
                    </div>
                </section>

                {/* Contact Section */}
                <section className="space-y-6">
                    <h2 className="text-xl font-bold text-white border-b border-border pb-2">Contact Info</h2>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        <div>
                            <label className="block text-sm font-medium text-secondary mb-1">Email</label>
                            <input
                                value={formData.contact.email}
                                onChange={(e) => handleChange('contact', 'email', e.target.value)}
                                className="w-full bg-surfaceHighlight border border-border rounded p-2 text-primary focus:outline-none focus:border-accent"
                            />
                        </div>
                        <div>
                            <label className="block text-sm font-medium text-secondary mb-1">Phone</label>
                            <input
                                value={formData.contact.phone}
                                onChange={(e) => handleChange('contact', 'phone', e.target.value)}
                                className="w-full bg-surfaceHighlight border border-border rounded p-2 text-primary focus:outline-none focus:border-accent"
                            />
                        </div>
                        <div>
                            <label className="block text-sm font-medium text-secondary mb-1">LinkedIn URL</label>
                            <input
                                value={formData.contact.linkedin}
                                onChange={(e) => handleChange('contact', 'linkedin', e.target.value)}
                                className="w-full bg-surfaceHighlight border border-border rounded p-2 text-primary focus:outline-none focus:border-accent"
                            />
                        </div>
                        <div>
                            <label className="block text-sm font-medium text-secondary mb-1">GitHub URL</label>
                            <input
                                value={formData.contact.github}
                                onChange={(e) => handleChange('contact', 'github', e.target.value)}
                                className="w-full bg-surfaceHighlight border border-border rounded p-2 text-primary focus:outline-none focus:border-accent"
                            />
                        </div>
                        <div>
                            <label className="block text-sm font-medium text-secondary mb-1">Resume URL (Direct PDF Link)</label>
                            <input
                                value={formData.contact.resume}
                                onChange={(e) => handleChange('contact', 'resume', e.target.value)}
                                className="w-full bg-surfaceHighlight border border-border rounded p-2 text-primary focus:outline-none focus:border-accent"
                                placeholder="https://example.com/resume.pdf or /uploads/resume.pdf"
                            />
                        </div>
                    </div>
                </section>

                <button
                    type="submit"
                    disabled={loading}
                    className="flex items-center gap-2 px-8 py-3 bg-white text-black font-bold rounded hover:bg-gray-200 transition-colors disabled:opacity-50"
                >
                    <Save size={20} />
                    {loading ? 'Saving...' : 'Save Settings'}
                </button>
            </form>
        </div>
    );
}
